<template>
  <astro-section header="料金">
    <table class="table table-bordered">
      <thead>
        <tr>
          <th v-for="col in cols" class="text-center">
            {{col}}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="row in rows" class="text-center">
          <td>{{row.kind}}</td>
          <td>{{row.price}}</td>
        </tr>
      </tbody>
    </table>
  </astro-section>
</template>

<script>
import astroSection from '../organisms/Section.vue'

export default {
  name: 'Price',
  data: () => ({
    cols: ['種別', '料金'],
    rows: [
      { kind: '大人', price: '2,900円' },
      { kind: '中・高生', price: '1,000円' },
      { kind: '小学生', price: '500円' },
    ],
  }),
  components: { astroSection },
}
</script>

<style>
.table {
  margin-left: auto;
  margin-right: auto;
}
</style>



